import React, { Component } from 'react';
import Layout from '../components/layout';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { graphql } from 'gatsby';

export const query = graphql`
	{
		prismic {
			allProjects {
				edges {
					node {
						name
						image
						description
						imageSharp {
							childImageSharp {
								fluid(maxWidth: 400, quality: 100) {
									src
									srcSet
									sizes
								}
							}
						}
					}
				}
			}
		}
	}
`;

export default class Projects extends Component {
	state = {
		projects: this.props.data.prismic.allProjects.edges
	};
	render() {
		return (
			<Layout>
				<div className="projects">
					<h1>Projects</h1>
					<Row>
						{this.state.projects.map((project) => {
							return (
								<Col xs={12} sm={6} md={4} lg={3}>
									<div className="project">
										<div className="project-img">
											<img
												src={project.node.imageSharp.childImageSharp.fluid.src}
												srcset={project.node.imageSharp.childImageSharp.fluid.srcSet}
												sizes={project.node.imageSharp.childImageSharp.fluid.sizes}
											/>
										</div>
										<div className="project-info">
											<div className="project-name">{project.node.name[0].text}</div>
											<div className="project-desc">{project.node.description[0].text}</div>
										</div>
									</div>
								</Col>
							);
						})}
					</Row>
				</div>
			</Layout>
		);
	}
}
